                this.$set(this, 'list', [
                    {'first_letter': '#', 'name': ''},
                    {'first_letter': '#', 'name': ''},
                    {'first_letter': '#', 'name': ''},
                    {'first_letter': 'B', 'name': 'A'},
                    {'first_letter': 'B', 'name': 'B'},
